


#root {
    background-color: rgba(35, 35, 35, 1);
    padding-left: calc(2vw);
    padding-top: calc(5vw);
}

body{
    margin: 0;
    background-color: rgba(35, 35, 35, 1);
}

.Tile {
    width: calc(10vw);
    height: calc(10vw);
    margin: calc(1vw);
    /* background-color: black; */
    background-size: calc(9vw) calc(9vw);
    border: none;

}

.tierRow{
    margin-bottom: calc(6vw);
}

.clearBtn{
    margin-bottom: 0;
}

.clearBtn > button{
    
    margin-left: calc(35vw);
    margin-bottom: calc(2vw);
    margin-top: calc(7vw);
    width: calc(30vw);
    height: calc(10vw);
    background-color: green;
    color: black;
    font-size: calc(4vw);
    border: 0;
    padding: 0;
}

button:focus {outline:0;}

